  @-webkit-keyframes glitch-effect {
    0%  {opacity: 1; margin-left: 20px;}
    25%  {opacity: 0;}
    50%  {opacity: 1; -webkit-filter: hue-rotate(90deg); filter: hue-rotate(90deg); margin-left: 40px;}
    75%  {opacity: 0;}
    100%  {opacity: 1; margin-left: 20px;}
  }
  @keyframes glitch-effect {
    0%  {opacity: 1; margin-left: 20px;}
    25%  {opacity: 0;}
    50%  {opacity: 1; -webkit-filter: hue-rotate(90deg); filter: hue-rotate(90deg); margin-left: 40px;}
    75%  {opacity: 0;}
    100%  {opacity: 1; margin-left: 20px;}
  }

@-webkit-keyframes fade {
  from { opacity: 1; }
  to { opacity: 0; }
}

@keyframes fade {
  from { opacity: 1; }
  to { opacity: 0; }
}

@-webkit-keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

div#enter {
  -webkit-animation: fadeIn 4000ms;
          animation: fadeIn 4000ms;
}

div#exit {
  -webkit-animation: fade 6000ms;
          animation: fade 6000ms;
}

.empty {
  -webkit-filter: grayscale(.9);
          filter: grayscale(.9);
}

